
import { defineComponent, reactive, ref, toRefs } from "vue";
import { useRouter } from "vue-router";
import { useStore } from "vuex";
import { SearchOutlined } from "@ant-design/icons-vue";

import data from "../mock/movie";
// import HelloWorld from "@/components/HelloWorld.vue"; // @ is an alias to /src

export default defineComponent({
  name: "Home",
  components: {
    SearchOutlined,
    // HelloWorld,
  },
  setup() {
    const router = useRouter();
    const store = useStore();

    const state = reactive({
      searchText: "",
      searchedColumn: "",
    });

    const searchInput = ref();

    const columns = [
      {
        title: "",
        dataIndex: "img",
        key: "img",
        width: 100,
      },
      {
        title: "",
        dataIndex: "title",
        key: "title",
        width: 400,
        customFilterDropdown: true,
        onFilter: (value: string, record: any) =>
          record.title.toString().toLowerCase().includes(value.toLowerCase()),
        onFilterDropdownVisibleChange: (visible: any) => {
          if (visible) {
            setTimeout(() => {
              searchInput.value.focus();
            }, 100);
          }
        },
      },
      {
        title: "IMDb",
        dataIndex: "imdb",
        key: "imdb",
        width: 100,
        customFilterDropdown: true,
        onFilter: (value: string, record: any) =>
          record.imdb.toString().toLowerCase().includes(value.toLowerCase()),
        onFilterDropdownVisibleChange: (visible: any) => {
          if (visible) {
            setTimeout(() => {
              searchInput.value.focus();
            }, 100);
          }
        },
        sorter: {
          compare: (a: any, b: any) => a.imdb - b.imdb,
          multiple: 3,
        },
      },
      {
        title: "",
        dataIndex: "douban",
        key: "douban",
        width: 100,
        customFilterDropdown: true,
        onFilter: (value: string, record: any) =>
          record.douban.toString().toLowerCase().includes(value.toLowerCase()),
        onFilterDropdownVisibleChange: (visible: any) => {
          if (visible) {
            setTimeout(() => {
              searchInput.value.focus();
            }, 100);
          }
        },
        sorter: {
          compare: (a: any, b: any) => a.douban - b.douban,
          multiple: 3,
        },
      },
      {
        title: "",
        dataIndex: "time",
        key: "time",
        width: 100,
        customFilterDropdown: true,
        onFilter: (value: string, record: any) =>
          record.time.toString().toLowerCase().includes(value.toLowerCase()),
        onFilterDropdownVisibleChange: (visible: any) => {
          if (visible) {
            setTimeout(() => {
              searchInput.value.focus();
            }, 100);
          }
        },
        sorter: {
          compare: (a: any, b: any) => a.time - b.time,
          multiple: 3,
        },
      },
      {
        title: "",
        dataIndex: "category",
        key: "category",
        width: 200,
        customFilterDropdown: true,
        onFilter: (value: string, record: any) =>
          record.category
            .toString()
            .toLowerCase()
            .includes(value.toLowerCase()),
        onFilterDropdownVisibleChange: (visible: any) => {
          if (visible) {
            setTimeout(() => {
              searchInput.value.focus();
            }, 100);
          }
        },
      },
      {
        title: "",
        dataIndex: "year",
        key: "year",
        width: 100,
        customFilterDropdown: true,
        onFilter: (value: string, record: any) =>
          record.year.toString().toLowerCase().includes(value.toLowerCase()),
        onFilterDropdownVisibleChange: (visible: any) => {
          if (visible) {
            setTimeout(() => {
              searchInput.value.focus();
            }, 100);
          }
        },
        sorter: {
          compare: (a: any, b: any) => a.year - b.year,
          multiple: 3,
        },
      },
      {
        title: "",
        dataIndex: "areas",
        key: "areas",
        width: 200,
        customFilterDropdown: true,
        onFilter: (value: string, record: any) =>
          record.areas.toString().toLowerCase().includes(value.toLowerCase()),
        onFilterDropdownVisibleChange: (visible: any) => {
          if (visible) {
            setTimeout(() => {
              searchInput.value.focus();
            }, 100);
          }
        },
      },
      {
        title: "",
        dataIndex: "language",
        key: "language",
        width: 150,
        customFilterDropdown: true,
        onFilter: (value: string, record: any) =>
          record.language
            .toString()
            .toLowerCase()
            .includes(value.toLowerCase()),
        onFilterDropdownVisibleChange: (visible: any) => {
          if (visible) {
            setTimeout(() => {
              searchInput.value.focus();
            }, 100);
          }
        },
      },
      {
        title: "",
        key: "action",
        fixed: "right",
        width: 150,
      },
    ];

    const handleSearch = (
      selectedKeys: string,
      confirm: any,
      dataIndex: string
    ) => {
      confirm();
      state.searchText = selectedKeys[0];
      state.searchedColumn = dataIndex;
    };

    const handleReset = (clearFilters: any) => {
      clearFilters();
      state.searchText = "";
    };

    const goDetail = (record: any) => {
      store.commit("SET_DATA", record);
      router.push({ name: "Detail", params: record });
    };

    return {
      data,
      columns,
      handleSearch,
      handleReset,
      searchInput,
      goDetail,
      ...toRefs(state),
    };
  },
});
